.glass3d {
  --filter-glass3d: blur(10px) brightness(.67) saturate(2.5);
  --color-glass3d: #7d44ab1a;
  --noise-glass3d: url("https://www.transparenttextures.com/patterns/egg-shell.png");
  z-index: 0;
  position: relative;
  box-shadow: 0 0 .75px #141a1f33, .7px .8px 1.2px -.4px #141a1f1a, 1.3px 1.5px 2.2px -.8px #141a1f1a, 2.3px 2.6px 3.9px -1.2px #141a1f1a, 3.9px 4.4px 6.6px -1.7px #141a1f1a, 6.5px 7.2px 10.9px -2.1px #141a1f1a, 8px 9px 14px -2.5px #141a1f33;
}

.glass3d:before {
  content: "";
  pointer-events: none;
  z-index: 3;
  backdrop-filter: var(--filter-glass3d);
  background-color: var(--color-glass3d);
  background-image: var(--noise-glass3d);
  background-repeat: repeat;
  background-size: 100px;
  border-radius: 20px;
  position: absolute;
  inset: 0;
  overflow: hidden;
}

.glass3d:after {
  content: "";
  pointer-events: none;
  z-index: 5;
  border-radius: 20px;
  position: absolute;
  inset: 0;
  overflow: hidden;
  box-shadow: inset 2px 2px 1px -3px #e0e6ebcc, inset 4px 4px 2px -6px #e0e6eb4d, inset 1.5px 1.5px 1.5px -.75px #e0e6eb26, inset 1.5px 1.5px .25px #e0e6eb08, inset 0 0 .25px .5px #e0e6eb08;
}

.glass3d > * {
  z-index: 6;
  position: relative;
}

